.page {
  padding-bottom: 10px;
}
.article {
  padding: 10px;
  background-color: white;
}
.article p {
  margin-top: 0;
}
.list-title {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 10px;
  background-color: white;
  padding: 10px;
}
.previous-next-bar {
  margin-top: 10px;
  padding: 10px;
  background-color: white;
}
#btnTop {
  display: inline-block;
  position: fixed;
  right: 10px;
  bottom: 10px;
  border-radius: 50%;
  background-color: #34538b;
  padding: 10px;
  cursor: pointer;
  display: none;
  opacity: 0.6;
}